import { Component, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { FormControl, Validators, ValidationErrors } from '@angular/forms';
import { Car, CarModel } from './models';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { RestApi } from './rest-api.service';
import { promise } from 'protractor';
import { identifierModuleUrl } from '@angular/compiler';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ActivatedRoute } from '@angular/router';

@Component({
  styleUrls: ['CarListPage.scss'],
  templateUrl: 'CarListPage.html' 
})

export class CarListPage implements OnInit { 

  minLength: number = 3;
  price: number = 0;
  
  editCar:Car = new Car('','',0);

  listModels:CarModel[]=[];
  listCars:Car[];

  modelsFilter:FormControl = new FormControl([]);

  @ViewChild("carEditorTemplate")
  carEditorTemplate:TemplateRef<void>;
  
  dialog:MatDialogRef<void, any>|undefined;

  constructor(
     private readonly restApi:RestApi,
     private readonly dialogSvc: MatDialog,
     private route: ActivatedRoute){ 
   }
  
  displayedColumns: string[] = ['name', 'vin', 'price', 'model', 'actions'];
  displayedColumnsData: string[] = ['name', 'vin', 'price', 'model'];
  dataSource = new MatTableDataSource<Car>();

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;

  ngOnInit() {
    this.getListCars();
    this.getListModels();
  }

  async getListModels() {
    this.listModels = await this.restApi.getModels();
  }

  async getListCars(){
    console.log("getListCars");
    this.dataSource.paginator = this.paginator;
    const promiseCars = this.restApi.getCars();
    this.listCars = await promiseCars;
    this.dataSource.data = this.listCars;
    this.price = this.listCars.map(t => t.price).reduce((sum, value) => sum + value, 0);
  }

  async addUpdateCar(isAdded:boolean) {
    
    if(isAdded){
      this.listCars.unshift(this.editCar);
    }

    this.clearCar();
    this.price = this.listCars.map(t => t.price).reduce((sum, value) => sum + value, 0);
    this.dataSource.data = this.listCars;
    
    if(this.dialog){
      this.dialog.close();
      this.dialog = undefined;
    }

  }
  
  openDialog(){
    this.dialog = this.dialogSvc.open(this.carEditorTemplate);
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  filterListCarByModels(selectedModels:CarModel[]){
    const filteredCars:Car[]=[];

    if (selectedModels.length==0) {
      this.dataSource.data = this.listCars;
      return;
    }

    this.listCars.forEach(car =>{
      if (selectedModels.find(currentModel => currentModel.id == car.model.id)) {
        filteredCars.push(car);
      }
    });

    this.dataSource.data = filteredCars;
  }

  btnEditCar(car:Car){
    this.editCar = car;
    this.dialog = this.dialogSvc.open(this.carEditorTemplate);
  }

  deleteCar(deleteCar:Car){
    this.restApi.deleteCars(deleteCar);
    const index = this.listCars.indexOf(deleteCar, 0);
    this.listCars.splice(index, 1);
    this.price = this.listCars.map(t => t.price).reduce((sum, value) => sum + value, 0);
    this.dataSource.paginator = this.paginator;
  }

  clearCar(){
    this.editCar = new Car("","",0);
  }

}
