import { Component, Input, OnInit, EventEmitter, Output, Host } from '@angular/core';
import { FormControl, Validators, ValidationErrors } from '@angular/forms';
import { MatAutocompleteSelectedEvent } from '@angular/material/autocomplete';
import { CarModel, Car } from './models';
import { RestApi } from './rest-api.service';

const carModelValidator=(control:FormControl):ValidationErrors|null=>{
  if(!control.value) return {validator:"model is required"}
  if(typeof control.value=="string"){
    return {validator:"invalid model"}
  }
  return null;
}

@Component({
    selector: 'car-editor',
    templateUrl: 'CarEditorComponent.html',
    host: {style:'display:inline-block'}
  })
export class CarEditorComponent implements OnInit{
  carNameControl=new FormControl("",Validators.required);
  vinControl=new FormControl("",Validators.required);
  modelControl=new FormControl("",carModelValidator);
  priceControl=new FormControl("",Validators.required);
  @Output("value-submit")
  readonly valueSubmited=new EventEmitter<boolean>();

  async ngOnInit() {
    const promiseModels = this.restApi.getModels();
    this.modelList = await promiseModels;
  }

  private _editCar:Car;
  
  @Input("value")
  get editCar():Car{
    return this._editCar;
  }
  set editCar(car:Car){
  
    if(car==undefined) throw new Error('the attribute value must be a Car object');
    this._editCar = car;
    this.carNameControl.reset(car.name);
    this.vinControl.reset(car.vin);
    this.modelControl.reset(car.model||"");
    this.priceControl.reset(car.price);
  
  }

  minLength: number = 3;
  price: number = 0;

  showModels:CarModel[];
  modelList:CarModel[] = [];

  constructor(private restApi:RestApi){

    this.modelControl.valueChanges.subscribe((value:string|CarModel)=>
    {
      if(typeof (value)==="string"){
        const v=value.trim().toLowerCase(); 
        const filteredList = this.modelList.filter(model => model.name.toLowerCase().startsWith(v));
        if(filteredList.length==1 && filteredList[0].name.length==v.length){
          this.modelControl.setValue(filteredList[0]);
          this.showModels = [];
          return;
        }
        this.showModels = value.length === 0 ? this.modelList : this.modelList.filter(model => model.name.toLowerCase().startsWith(v));
      }
    })
  }

  async submitCar() {
    this.carNameControl.markAllAsTouched();
    this.vinControl.markAllAsTouched();
    this.priceControl.markAllAsTouched();
    this.modelControl.markAllAsTouched();

    if(this.carNameControl.invalid 
      || this.vinControl.invalid 
      || this.priceControl.invalid 
      || this.modelControl.invalid) return;

    this._editCar.name = this.carNameControl.value; 
    this._editCar.vin = this.vinControl.value; 
    this._editCar.price = this.priceControl.value;
    this._editCar.model = this.modelControl.value;

    try {
      if(!this._editCar.id){
        this._editCar.id = await this.restApi.addCars(this._editCar); 
        this.valueSubmited.emit(true); 
      }else{
        await this.restApi.updateCars(this._editCar);
        this.valueSubmited.emit(false);
      }
    } catch (error) {
      console.error(error);
    }
    
  }

  getModelTitle(value:CarModel|string){
    if(typeof value === "string") return value;
    return value.name;
  }

}